import React from 'react'
import Component from './someComponent'

import CardWrapper from '../../common/Card'
import SmallTitle from '../../common/typografy/smallTitle'
import Divider from '../../common/divider'
import withLogin from './withLogin'
import withPropsStyles from './withPropsStyles'

const HOCExample = () => {
    const ComponentWithAuth = withLogin(Component)
    const ComponentWithPropsStyles = withPropsStyles(Component)
    const NewComponent = withPropsStyles(ComponentWithAuth)

    return (
        <>
            <CardWrapper>
                <SmallTitle>1. Обычный компонент</SmallTitle>
                <Divider />
                <Component />
            </CardWrapper>
            <CardWrapper>
                <SmallTitle>2. Функциональный HOC</SmallTitle>
                <Divider />
                <ComponentWithAuth />
            </CardWrapper>
            <CardWrapper>
                <SmallTitle>3. HOC With Styles and Props</SmallTitle>
                <ComponentWithPropsStyles />
            </CardWrapper>
            <CardWrapper>
                <SmallTitle>4. Composed HOC</SmallTitle>
                <NewComponent />
            </CardWrapper>
        </>
    )
}

export default HOCExample

// HOC - функция, которая принимает компонент и возвращает: новый коспонент, обновленный компонент, другой компонент, редирект и т.д. Т.е. возвращает что-то, в зависимости от логики, которую мы заключаем в эту функцию

// HOC может содержать логические и стилистические итератеры

// Функционал HOC - разделять композицию и использовать один и тот же композиционный функционал в нескольких местах
